.dialog {
    margin-bottom: 10px;
}
.dialogWrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.dialog a {
    text-decoration: none;
    color: beige;
}
a.active {
    color: gold;
}
.image {
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 10px;
}